/**
 * 路由，分为全局路由和需要过滤的路由
 */

 // 控制中心路由，用户、
import controlCenterPageRouterMap from './../views/controlCenter/control-center-router'
import userPageRouterMap from './../views/user/user-router'
// 交易员
import traderPageRouterMap from './../views/traders/traders-router'
// demo页面文件夹
// import demoPageRouterMap from './../views/demo-page/demo-router'

const indexRouterMap = [
  {
    path: '/login',
    meta: {
      title: '登录'
    },
    name: 'login',
    component: resolve => {
      require(['../views/login/index'], resolve)
    },
    hidden: true
  },
  {
    path: '/',
    alias: '/index',
    meta: {
      title: '首页'
    },
    name: 'index',
    component: resolve => {
      require(['../views/index/index'], resolve)
    },
    hidden: true
  },
  // {
  //   path: '/traders',
  //   // alias: '/index',
  //   meta: {
  //     title: '交易员'
  //   },
  //   name: 'index',
  //   component: resolve => {
  //     require(['../views/traders/index'], resolve)
  //   },
  //   hidden: true
  // },
  {
    path: '*',
    redirect: '404',
    name: 'not_found',
    hidden: true
  }
]

let asyncRouterMap = []

// 拼装
asyncRouterMap = asyncRouterMap
.concat(controlCenterPageRouterMap)
.concat(userPageRouterMap)
.concat(traderPageRouterMap)

export {indexRouterMap,asyncRouterMap}


