// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
// css
require('!style-loader!css-loader!less-loader!./assets/style/index.less')

import Vue from 'vue'
import App from './App'
import store from './store'
// import ds from './common/ds'
// 进度条
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import VueRouter from 'vue-router'

// 主路由
import { indexRouterMap } from './router/index-router'

Vue.use(VueRouter)
Vue.config.productionTip = false
Vue.use(ElementUI)

let routes = []
routes = indexRouterMap

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  NProgress.start()
  window.document.title = to.meta.title
  // 全权访问不做检测
  if(
    to.path.indexOf('/login')!==-1
  ){
    next()
  }else{
    // 检测token
    let a = false
    if(a){
      // 如果没有权限路由过滤信息
      if(!store.getters.fundFirm){
        // await store.dispatch('App_GetFundFirm').then(routerData => {
        //   router.addRoutes(routerData)
        //   next({...to})
        // })
        store.dispatch('App_GetFundFirm').then(routerData => {
          router.addRoutes(routerData)
          next()
        })
      }else{
        next()
      }
    }else{
      next({path: '/login'})
      NProgress.done()
    }
  }
})

router.afterEach(()=>{
  NProgress.done()
})

/* 启动app */
window.duyangsir = new Vue({
  router,
  template: '<App/>',
  components: { App }
}).$mount('#app')


